<template lang="html">
  <el-dialog :visible.sync="dialogVisible" title="" width="400px">
    <el-form>
      <el-form-item label="" label-width="80px">
        <el-select
          v-model="selectedGroupId"
          placeholder=""
          size="small"
          style="width: 240px"
        >
          <el-option
            v-for="item in options"
            :key="item.groupId"
            :label="item.name"
            :value="String(item.groupId)"
          />
        </el-select>
      </el-form-item>
    </el-form>

    <span slot="footer">
      <el-button @click="confirmGroupChange" type="primary" size="small"></el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  name: 'DialogGroupSetting',
  props: {
    user: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      dialogVisible: false,
      options: [],
      selectedGroupId: this.user.groupIds
    }
  },
  watch: {
    user () {
      this.selectedGroupId = this.user.groupIds
    }
  },
  created () {
    this.fetchUserGroups()
  },
  methods: {
    // idnameidgroudNames
    confirmGroupChange () {
      this.dialogVisible = false

      this.$httpAPI.postUserGroupSetting({
        userId: this.user.id,
        groupId: this.selectedGroupId
      }).then((response) => {
        if (response.data.status === 200) {
          this.$message.success('')
          this.$emit('on-group-changed')
        } else {
          this.$message.error('')
        }
      }).catch(error => console.log(error))
    },
    // 
    toggleDialogVisible (status) {
      this.dialogVisible = true
    },
    // 
    fetchUserGroups () {
      this.$httpAPI.fetchUserGroups({
        params: { pageNo: 1, pageSize: 100 }
      }).then((response) => {
        this.options = response.data.data
      }).catch((error) => console.log(error))
    }
  }
}
</script>
