<template lang="html">
  <el-dialog :visible.sync="dialogVisible" title="" width="80%" style="min-width: 760px">
    <!--  -->
    <el-form :model="searchData" size="small" inline>
      <FormInput
        @keyup.native.enter="$emit('on-search', formData)"
        @on-change="$set(formData, 'username', $event)"
        label=""
        width="174px"
        ref="username"
      />

      <FormInput
        @keyup.native.enter="$emit('on-search', formData)"
        @on-change="$set(formData, 'drawno', $event)"
        label=""
        width="174px"
        ref="drawno"
      />

      <FormSelectGame
        @on-change="$set(formData, 'gameType', $event)"
        httpAPIName="fetchGamesMenu"
        :httpAPIParams="{ params: { type: 1 } }"
        labelAttr="name"
        valueAttr="id"
        prop="gameType"
        label=""
        filterable
        ref="gameType"
      />

      <FormSelectStatic
        @on-change="$set(formData, 'status', $event)"
        :options="[
          { value: 0, label: '' },
          { value: 1, label: '' },
          { value: 2, label: '' }
        ]"
        label=""
        width="100px"
        ref="status"
      />

      <div style="display: inline-block">
        <SearchIcon @click.native="search" />
        <SearchReset @click.native="reset" />
      </div>
    </el-form>

    <!--  -->
    <div>
      <!--  -->
      <el-table :data="tableData" size="small" highlight-current-row border>

        <el-table-column type="index" :width="36" />

        <el-table-column prop="id" label="ID" :min-width="150" />

        <el-table-column prop="currentDrawno" label="" :min-width="90" />

        <el-table-column prop="gameName" label="" :min-width="120" />

        <el-table-column prop="username" label="" :width="100" />

        <el-table-column prop="totalBets" label="" :width="90">
          <template slot-scope="scope">
            <span>{{ scope.row.totalBets }}</span>
            <BaseIcon @on-click="showDialog(scope.row, 'dialogDetail')" class="pull-right" />
          </template>
        </el-table-column>

        <el-table-column prop="totalAmount" label="" :min-width="80">
          <template slot-scope="scope">
            <span>{{ scope.row.totalAmount | RMB }}</span>
          </template>
        </el-table-column>

        <el-table-column prop="totalAwardAmount" label="" :min-width="80">
          <template slot-scope="scope">
            <span>{{ scope.row.totalAwardAmount | RMB }}</span>
          </template>
        </el-table-column>

        <el-table-column prop="status" label="" :min-width="70">
          <template slot-scope="scope">
            <span>{{ scope.row.status | lotteryOrderStatus }}</span>
          </template>
        </el-table-column>

        <el-table-column prop="createDate" label="" :min-width="140">
          <template slot-scope="scope">
            <span>{{ scope.row.createDate | time }}</span>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <BasePagination
        @on-change="handlePaginationChange"
        :page="page"
        :httpURL="tableAPI"
        :requestParams="requestParams"
      />
    </div>

    <!--  -->
    <DialogBetsDetail :id="activeItem.id" ref="dialogDetail" />
  </el-dialog>
</template>

<script>
import BaseIcon from '@/components/base/BaseIcon'
import DialogBetsDetail from '@/components/dialog/DialogBetsDetail'

import FormInput from '@/components/form/FormInput'
import FormSelectGame from '@/components/form/FormSelectGame'
import FormSelectStatic from '@/components/form/FormSelectStatic'
import SearchIcon from '@/components/search/SearchIcon'
import SearchReset from '@/components/search/SearchReset'
import BasePagination from '@/components/base/BasePagination'
import BasePopoverTextarea from '@/components/base/BasePopoverTextarea'

export default {
  name: 'UsersListDialogLogs',
  components: {
    FormInput,
    FormSelectGame,
    FormSelectStatic,
    SearchIcon,
    SearchReset,
    BasePopoverTextarea,
    BasePagination,
    BaseIcon,
    DialogBetsDetail
  },
  props: {
    userId: {
      type: [ String, Number ],
      required: true
    }
  },
  data () {
    return {
      dialogVisible: false,
      searchData: {},
      activeItem: { id: '' },
      tableData: [],
      tableAPI: 'fetchLotterBetsList',
      requestParams: { userId: this.userId, pageNo: 1, pageSize: 10 },
      page: { current: 1, size: 10, total: 10 }
    }
  },
  watch: {
    userId () {
      this.page.current = 1
      this.$set(this.requestParams, 'userId', this.userId)

      this.fetchUserBets(this.userId)
    }
  },
  methods: {
    // 
    handlePaginationChange (data) {
      this.tableData = data
    },
    fetchUserBets (userId) {
      this.$httpAPI[this.tableAPI]({
        params: { userId, pageNo: 1, pageSize: 10 }
      }).then(response => {
        this.page.total = response.data.amount

        if (response.data.data) {
          this.tableData = response.data.data
        } else {
          this.tableData = []
          this.$message.info('')
        }
      }).catch(error => console.log(error))
    },
    // 
    search () {
      this.$emit('on-search', this.formData)
    },
    //  form 
    reset () {
      for (let key in this.$refs) {
        this.$refs[key].reset()
      }

      this.$utils.initializeObjectProperties(this.formData)
    },
    // 
    showDialog (item, ref) {
      this.activeItem = item

      this.$refs[ref].toggleDialogVisible(true)
    },
    // 
    toggleDialogVisible (status) {
      this.dialogVisible = status
    }
  }
}
</script>
